header {
  background-color: white;
}

header section {
  height: 100vh;
}

header .cls-1 {
  fill: black;
}

header img {
  width: 100%;
  height: 100%;
}

header h3 {
  width: 40%;
  position: absolute;
  top: 35%;
  left: 70%;
  transform: translate(-50%, -50%);
  font-size: 5vh;
  animation-name: ckw0;
  animation-duration: 7s;
  transform-origin: 50% 50%;
  color: black;
}

header svg {
  width: 30%;
  position: absolute;
  top: 40%;
  left: 30%;
  transform: translate(-50%, -50%);
}

#path1 {
  animation-name: ckw1;
  animation-duration: 5s;
  transform-origin: 50% 50%;
}

#path2 {
  animation-name: ckw2;
  animation-duration: 5s;
  transform-origin: 50% 50%;
}

#path3 {
  animation-name: ckw0;
  animation-duration: 5s;
  transform-origin: 50% 50%;
}

#path4 {
  animation-name: ckw0;
  animation-duration: 5.5s;
  transform-origin: 50% 50%;
}

#path5 {
  animation-name: ckw0;
  animation-duration: 6s;
  transform-origin: 50% 50%;
}

#path6 {
  animation-name: ckw0;
  animation-duration: 6.5s;
  transform-origin: 50% 50%;
}

@keyframes ckw0 {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes ckw1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes ckw2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

nav {
  z-index: 2000;
  position: absolute;
  top: 94.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  background-color: black;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0px 5%;
}

nav ul li a {
  font-size: 25px;
  color: white;
  text-decoration: none;
  height: 100%;
  line-height: 2;
  width: 100%;
}

nav ul li a::after, nav ul li a::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: white;
  transform: scale(0);
  transition: transform 0.2s ease-in-out;
}

@media screen and (max-width: 1200px) {
  ul li a {
    font-size: .8em !important;
  }
}

@media screen and (max-width: 900px) {
  ul li a {
    font-size: .7em !important;
  }
}

@media screen and (max-width: 750px) {
  ul li a:nth-child(2) {
    display: none;
  }
}

nav li a:hover::after, a:hover::before {
  transform: scale(1);
}

.footer-flex {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.footer-flex div {
  width: 80%;
}

.footer-flex a {
  text-decoration: none;
  color: black;
  padding: 5px 10px;
  border-radius: 10px;
}

.footer-flex a:hover {
  color: white;
  background-color: black;
}

.footer-flex h2 {
  margin: 0;
}

.footer-flex .informations iframe {
  height: 200px;
}

ul {
  margin-right: 10px;
  padding: 0;
}

li {
  padding: 5px 0px;
  list-style-type: none;
  font-size: 1em;
}

.flex {
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.prestation {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.prestation .card {
  width: 300px;
  height: 500px;
  border-radius: 10px;
  text-overflow: none;
  margin: 0 10px;
  margin-bottom: 20px;
  overflow: hidden;
  box-shadow: 0px 0px 0px black;
  transition: .5s;
  border: solid 5px black;
  cursor: pointer;
}

.prestation .card .card-img {
  overflow: hidden;
}

.prestation .card .card-img img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 1s;
}

.prestation .card .card-title {
  padding: 0;
  margin: 0;
}

.prestation .card .card-body {
  padding: 0px 20px;
  margin: 0;
}

.prestation .card:hover {
  box-shadow: 0px 1px 20px black;
}

.prestation .card:hover img {
  transform: scale(1.5);
}

.btn {
  background-color: #363636;
  border: solid 3px black;
  border-radius: 15px;
  color: white;
  padding: 15px 30px;
  display: inline-block;
  font-size: 20px;
  text-decoration: none;
  transition: .5s;
}

.btn:hover {
  background-color: black;
}

.introduction {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.introduction img {
  padding-top: 30px;
  width: 400px;
}

.introduction div {
  text-align: justify;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

.galerie {
  margin-bottom: 50px;
}

.each-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
}

.spotify article {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.spotify article div {
  width: 125px;
  padding: 10px;
}

.spotify article img {
  width: 100%;
  box-shadow: 0px 1px 10px black;
  cursor: pointer;
}

.spotify article img:hover {
  transition: 1s;
  transform: scale(1.5);
  box-shadow: 0px 1px 20px black;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: apotek, sans-serif;
  font-weight: 100;
  font-size: 1.5em;
}

.container {
  padding: 0 200px;
}

@media screen and (max-width: 750px) {
  .container {
    padding: 0 25px;
  }
}

@media screen and (max-width: 1200px) {
  .container {
    padding: 0 100px;
  }
}

span {
  height: 2px;
  width: 100%;
  background-color: black;
  display: block;
}

#presentation, #prestation, #location, #contact, #decouvrir, #galerie {
  padding-top: 10vh;
}
